.popup {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    display: flex;
    visibility: hidden;
    opacity: 0;
    transition: opacity .7s ease;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}